/*重写ivew导航栏的样式*/
#slidermenu{
  .ivu-menu:after{
    width: 0!important;
  }
  .ivu-menu-vertical .ivu-menu-item{
     padding: 5px 15px!important;
     background: #fff;
   }
  .ivu-menu-vertical .ivu-menu-item:hover,#slidermenu  .ivu-menu-vertical .ivu-menu-submenu-title:hover {
    color: #fff!important;
    background: #96D8FA;
  }
  .ivu-menu-submenu  .ivu-menu-submenu-title{
    padding: 5px 15px!important;
  }
  .ivu-menu-opened .ivu-menu-submenu-title{
    color: #fff!important;
    background: #96D8FA;
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu){
    background: #E2E2E2!important;
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):after{
    width: 0!important;
  }
  .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):before{
    content: '';
    display: block;
    width: 8px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    background: #7ED2EA;
    border-right: #ECECEC 2px solid;
    box-shadow: 2px 0 0 0 #7ED2EA;
  }
}

